$left_icons: ('Office', 'Word', 'Excel', 'PowerPoint', 'Outlook', 'OneDrive', 'OneNote', 'ToDo', 'Calendar', 'Skype');
$dots: ('nine-dots', 'three-dots');
$home_icons: ('voice', 'search');

* {
  margin: 0;
  padding: 0;
  font-size: 16px;
}

li{
  list-style:none;
  margin: 0;
  padding: 0;
}

body {
  // height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: ivory;
  margin-top: 100px;
}

.home {
  display: flex;
  justify-content: center;
  overflow: hidden;
  position: relative;
  //image-rendering: -webkit-optimize-contrast;
  background-image: url('../imgs/wallpapers/edge_image.jpg');
  background-size: cover;
  width: 1500px;
  height: 750px;
  border-radius: 10px;
  box-shadow: 10px 10px 20px 10px grey;
  z-index: 0;

  .left-rail {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    position: absolute;
    left: 0;
    border-right: 1px solid rgba(0, 0, 0, 0.09);
    background-color: #f7f7f7;
    height: 750px;
    width: 4%;
    transition: width 0.1s linear 0s;
    z-index: 1;
    
    li {
      background-size: cover;
      width: 24px;
      height: 24px;
      padding: 4px;
      transition: all 0.2s linear;
      &:hover{
        transform: scale(1.2);
        cursor: pointer;
      }
    }
    
    @each $icon in $left_icons{
      .#{$icon}{background-image: url('../imgs/icons/#{$icon}.svg');}
    };
  
    @each $dot in $dots{
      .#{$dot}{background-image: url('../imgs/icons/#{$dot}.png');}
    }
  
  }

  .high-bar{
    position: absolute;
    top: 100px;
    display: flex;
    align-items: center;
    gap: 15px;
    
    .select-box{
      width: 800px;
      height: 46px;
      padding: 0 15px;
      border-radius: 22px;
      outline: none;
      border: none;
      box-shadow : 0 1px 6px 0 rgba(32, 33, 36, .28);
      box-sizing: border-box;
  
      &:focus{
        border-radius: 5px;
      }
    }
  
    .voice{
      position: absolute;
      right: 45px;
      width: 24px;
      height: 24px;
      padding: 3px;
      background-size: cover;
      transition: all 0.2s linear;
    }
  
    .search{
      position: absolute;
      right: 15px;
      width: 24px;
      height: 24px;
      padding: 3px;
      background-size: cover;
      transition: all 0.2s linear;
    }
  
    @each $home_icon in $home_icons{
      .#{$home_icon}{background-image: url('../imgs/icons/#{$home_icon}.png');}
    }
  }

  .middle-bar {
    display: flex;
    justify-content: space-between;
    position: absolute;
    top: 230px;
    width: 800px;
    
    div {
      width: 200px;
      height: 200px;
      border-radius: 30px;
      background-color: white;
    }

    @each $home_icon in $home_icons{
      .#{$home_icon}{background-image: url('../imgs/icons/#{$home_icon}.png');}
    }
  }

  .bottom-bar {
    position: absolute;
    top: 500px;
    transition: opacity 0.3s ease-out 0s;
  
    ul{
      display: grid;
      grid-template-columns: repeat(8, auto);
      gap: 25px 50px;
  
      .content-container{
        display: flex;
        justify-content: center;
        align-items: center;
        width: 44px;
        height: 44px;
        background-color: #ffffff;
        border-radius: 10px;
      }
      
      span{
        background-size: cover;
        width: 24px;
        height: 24px;
        padding: 4px;
        transition: all 0.2s linear;
        &:hover{
          transform: scale(1.2);
          cursor: pointer;
        }
      }
    }
    
    @each $icon in $left_icons{
      .#{$icon}{background-image: url('../imgs/icons/#{$icon}.svg');}
    };
  }
}






.background-image-overlay{
  background-image: radial-gradient(rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.5) 100%), radial-gradient(rgba(0, 0, 0, 0) 33%, rgba(0, 0, 0, 0.3) 166%);
  width: 96%;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
  z-index: -1;
}
